@import "components/m_header";
@import "components/m_asside";
@import "components/m_footer";


@media screen and (max-width: 1200px), screen and (max-device-width: 1200px) {
  body{
    .content{
      .container{
        .row{
          .col-lg-9{
            .customRequest{
              &__form__headline{
                form{
                  ul{
                    li{
                      .important{
                        width: 200px;
                      }
                    }
                  }
                }
              }
            }
            .requestContact{
              &__headline{
                form{
                  ul{
                    .state{
                      label{
                        input{
                          width: 63%;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .aside{
              display: block;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 755px), screen and (max-device-width: 755px){
  body{
    .content{
      .container{
        .row{
          .col-sm-12{
            .customRequest{
              &__form__headline{
                form{
                  ul{
                    width: unset;
                    li{
                      flex-direction: column;
                      align-items: center;
                      p{
                        margin: 10px 0 5px;
                        width: unset!important;
                      }
                      label{
                        width: 90%;
                        select,
                        input{
                          width: 220px;
                        }
                      }
                    }
                  }
                }
              }
            }
            .requestContact{
              &__headline{
                form{
                  ul{
                    width: 100%;
                    li{
                      p{
                        display: none;
                      }
                      label{
                        width: 100%;
                        ::-webkit-input-placeholder{
                          color: #525252;
                        }
                        ::-moz-placeholder{
                          color: #525252;
                        }
                        :-moz-placeholder{
                          color: #525252;
                        }
                        :-ms-input-placeholder{
                          color: #525252;
                        }
                      }
                    }
                    .state{
                      label{
                        select{
                          margin: 0 10px 0 0;
                        }
                        input{
                          width: 85.5%;
                        }
                      }
                    }
                    .protectionCode{
                      flex-direction: column;
                      p{
                        display: block;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .customRequest__popup{
      padding: 5px 15px;
      width: 290px;
    }
  }
}
